.wrapper {
    margin: 10px;
    max-width: 800px;
    padding: 10px;
    margin-top: 0;
    padding-top: 0;
  }

  .header {
    color: #539c64;
    font-size: 20px;
    font-weight: 500;
    margin-top: 3rem;
    line-height: 1rem;
  }

  .innerWrapper {
    border: 1px solid #e2e2e2;
    padding: 1rem;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    gap: 2rem;
  }

  .formButton {
    width: fit-content;
  }

  .listWrapper {
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
    /* align-items: center; */
  }
  .searchContainer {
    position: relative;
    margin: 0 10px;
    padding: 0 10px;
  }
  .searchInput {
      width: 18rem;  
      background: #edeceb;
    border: none;
    outline: none;
    padding: 0.8rem;
    border-radius: 4px;
  }
  .searchInput:focus {
    outline: none;
    border: none;
  }
  .searchIcon {
    position: absolute;
    right: 1.2rem;
    top: 18%;
  }
  .paginationContainer {
    font-weight: 500;
    letter-spacing: 1;
    text-align: end;
    padding: 0 10px;
    color: #302c2c;
  }
  .paginationInner {
    display: flex;
    justify-content: end;
    margin-top: 5px;
    align-items: center;
  }
  .paginationControls {
    background: transparent;
    border: none;
    outline: none;
    color: #539c64;
    padding: 0 10px;
    cursor: pointer;
  }
  
  .paginationControls:disabled {
    color: #919191;
    cursor: not-allowed;
  }
  .errorMessage {
    text-align: center;
    color: red;
    font-size: 1.5rem;
  }
  